import React from "react";
import "./styles.css";

export default function App() {
  const [counter, setCounter] = React.useState(4);

  const chanecount = (value) => {
    // setCounter(counter+value,)

    setCounter((prev) => {
      if (counter == 10) {
        return 0;
      }
      return prev + value;
    });
  };

  //  if(counter>10){
  //    return <div>counter reached max</div>
  //  }

  return (
    <div className="App">
      <h1>counter</h1>
      <h2>{counter}</h2>
      <button onClick={() => chanecount(1)}>Add</button>
      <button onClick={() => chanecount(-1)}>Reduse</button>
      <div>counter is {counter % 2 === 0 ? "Even" : "odd"}</div>
    </div>
  );
}
